footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  background: #dbdbdbbb;
  border-top: 1px solid rgba(0, 0, 0, 0.144);
  padding: 0.85rem;
  display: flex;
  justify-content: center;

  a {
    display: flex;
    background: #dbdbdbbb;
    height: 2.7rem;
    width: 2.7rem;
    margin: 0 0.95rem;
    border-radius: 50%;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    box-shadow: inset 0 1px 0 rgba(219, 219, 219, 0.5),
      0 1px 1px rgba(0, 0, 0, 0.19);
    transition: transform 0.1s;

    i {
      margin-top: 0.1rem;
      font-size: 1.4rem;
      color: rgb(80, 80, 80);
      transition: transform 0.1s;
    }

    &:hover {
      box-shadow: inset 0px 1px 3px rgb(109, 109, 109);
    }
    &:hover i {
      transform: scale(0.9);
    }

    &:hover .fa-linkedin {
      color: #0a66c2;
    }
    &:hover .fa-github {
      color: #1a1e22;
    }
    &:hover .fa-instagram {
      color: #e73076;
    }
  }
}
